footer {
    margin-top: 1vmax;
    padding: 2vmax;
    background-color: rgb(34, 33, 33);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.midFooter {
    width: 60%;
    text-align: center;
    justify-content: center;
}

.midFooter > h1 {
    font-size: 4vmax;
    font-family: "Roboto", "Droid Sans", "Chilanka";
    color: #eb4034;
}
.midFooter > p {
    max-width: 60%;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
        sans-serif;
    margin: 1vmax auto;
}

